<template>
    <div class="wrap">
      <h1>5월 마지막주 영화 예매 순위</h1>
      <ul class="movies">
        <li v-for="movie in movies" class="item" :key="movie.id">
          <span class="rank">{{movie.id}}</span>
        </li>
      </ul>
    </div>
  </template>
  <script>
  export default {
    created () {         
      this.$http.get('/api/movies')
          .then((response) => {
            this.movies = response.data
          })
    },
    data () {
      return {
        movies: []
      }
    }
  }
  </script>
